.face {
    height: 180px;
    width: 130px;
    border-radius: 100px;
    background-color: var(--primary-color);
}

.eyes {
    display: flex;
    gap: 14px;
    position: relative;
    top: 40px;
    left: 18px;
}

.eye {
    display: block;
    height: 40px;
    width: 40px;
    background: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
}

.eye-ball {
    height: 20px;
    width: 20px;
    background: black;
    border-radius: 50%;
    transition: all 0.3s;
    position: relative;
    animation: eye-animation 10s infinite;
}

@keyframes eye-animation {
    0% {
        left: 0;
    }
    20% {
        left: 10px;
    }
    40%{
        left:0px;
    }
    60%{
        right: 0px;
    }
    70%{
        right: 10px;
    }
    80%{
        right: 0px;
    }
    100%{
        right: 0px;
    }
}

.eyelid {
    display: block;
    position: absolute;
    height: 50px;
    width: 50px;
    background: white;
    border-radius: 50%;
    transition: all 0.3s;
    height: 0;
}

.mouth {
    width: 80px;
    height: 30px;
    background: white;
    position: relative;
    top: 70px;
    left: 25px;
    transition: all 0.3s;

    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
}

.mouth::before{
    content: '';
    height: 21px;
    width: 108px;
    position: absolute;
    background: var(--primary-color);
    border: 0;
    border-radius: 50%;
    top: -7px;
    left: -14px;

}