.menu-container {
    height: 100vh;
    width: 100vw;
    position: fixed;
    transition: all 0.3s;
    visibility: hidden;
    opacity: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 24px;
}
.menu-container.open {
    position: absolute;
    background: white;
    z-index: 1;
    visibility: visible;
    opacity: 1;
}

.menu-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 16px;
    filter: opacity(0.5);
    cursor: pointer;
    transition: all .3s ease;
}

.menu-icon.active{
    filter: opacity(1);
}

.menu-icon:hover {
    filter: opacity(1);
}

.menu-icon img {
    height: 100px;
    width: 100px;
}

.menu-icon label {
    font-size: 32px;
}
